.cart-container {
    width: 90%;
    margin: auto;
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  .cart-header, .cart-item {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap: 10px;
  }
  
  .cart-header {
    font-weight: bold;
    border-bottom: 2px solid #ddd;
    padding-bottom: 10px;
    margin-bottom: 20px;
  }
  
  .cart-item {
    padding: 10px 0;
    background-color: #4ba07b;
  }
  
  .cart-item-image {
    width: 50px;
    height: 50px;
    object-fit: cover;
  }
  
  .cart-item-quantity {
    display: flex;
    align-items: center;
    gap: 5px;
  }
  
  .quantity-btn {
    background-color: #f0f0f0;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .cart-item-remove {
    color: red;
    cursor: pointer;
  }
  
  .cart-total {
    text-align: right;
    font-size: 14px;
    font-weight: bold;
    margin-top: 20px;
  }
  
  hr {
    border: none;
    border-top: 1px solid #ddd;
    margin: 10px 0;
  }
  