.loginsignup{
    width: 100%;
    background-color:#4ba07b;  
    padding-block: 50px;
}

.loginsignup-container{
    width: 580px;
    height: 600px;
    background: white;
    margin: auto;
    padding: 40px 60px;
}
.loginsignup-container h1{
    margin: 20px 0px;
}
.loginsignup-fields{
    display: flex;
    flex-direction: column;
    gap: 29px;
    margin-top: 30px;
}

.loginsignup-fields input{
    height: 72px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid cornsilk;
    outline: none;
    color: grey;
    font-size: 18px;
}

.loginsignup-container button{
    width: 500px;
    height: 72px;
    color: white;
    background:red ;
    margin-top: 30px;
    border: none;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
}

.loginsignup-login{
    margin-top: 20px;
    color: grey;
    font-size: 18px;
    font-weight: 500px;
}