
.shop-category-products {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.product-sort {
    text-align: center;
    font-size: 18px;
    margin-bottom: 20px;
}

.category {
    background-color: #4ba07b;
    padding: 20px;
    
}

.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}

.pagination button {
    background-color: #ffffff;
    border: 1px solid #4ba07b;
    color: #4ba07b;
    padding: 10px 20px;
    margin: 0 5px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
}

.pagination button:hover {
    background-color: #4ba07b;
    color: #ffffff;
}

.pagination button:disabled {
    background-color: #cccccc;
    color: #666666;
    cursor: not-allowed;
}

.pagination span {
    margin: 0 10px;
    font-size: 16px;
    color: #ffffff;
}
