


/* Item Container */
.item-container {
  background-color: #fbfbfb;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 20px;
  text-align: center;
  width: 250px;
  margin: 20px;
  transition: transform 0.3s ease;
  display: flex; 
  flex-direction: column;
  justify-content: space-between; 
}

.item-container:hover {
  transform: translateY(-5px);
}

/* Product Image */
.product-image {
  width: 100%;
  height: 250px;
  overflow: hidden;
  display: flex; 
  justify-content: center; 
  align-items: center; 
}

.product-image img {
  max-width: 100%; 
  max-height: 100%; 
  transition: transform 0.3s ease;
}

.product-image img:hover {
  transform: scale(1.1);
}

/* Product Title */
.product-title {
  font-size: 18px;
  font-weight: bold;
  margin-top: 10px;
}

/* Product Price */
.product-price {
  font-size: 16px;
  font-weight: bold;
  color: #007bff;
  margin-top: 5px;
}

/* Product Description */
.product-description {
  font-size: 14px;
  margin-top: 10px;
}

/* Product Sizes */
.product-sizes {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.size-option {
  margin: 0 5px;
  padding: 8px 12px;
  border: 1px solid #000000;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.size-option:hover {
  background-color: #4ba07b;
  color: #fff;
}
