.popular-container{
  background-color: #4ba07b;
}
   
   
   
   /* Ad Title */
.ad-title {
    text-align: center;
    padding-block:20px ;
  }
  
  /* Ad Heading */
  .ad-heading {
    font-size: 48px;
    font-weight: bold;
   
  }
  
  .ad-heading svg {
    font-size: 32px;
    vertical-align: middle;
  }
  
  /* Ad Subtitle */
  .ad-subtitle {
    font-size: 16px;
    color: #6c757d;
  }
  
  /* Popular Item Container */
  .popular-item {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  /* Product Image */
  .product-image {
    width: 100%;
    height: 200px;
    overflow: hidden;
    display: flex; 
    justify-content: center; 
    align-items: center; 
  }
  
  .product-image img {
    max-width: 100%; 
    max-height: 100%; 
    object-fit: cover; 
    display: block; 
    transition: transform 0.3s ease;
  }
  
  .product-image img:hover {
    transform: scale(1.1);
  }
  