
.ecommerce-footer {
    background-color:#b4b4b4;
    color: #000000;
    padding: 20px 0;
    display: flex;
    justify-content: space-around;
    
  }
  .footer-section ul {
    list-style: none;
    padding: 0;
  }
  .footer-section ul li a,p {
    color:black;
    text-decoration: none;
    font-size: 14px;
  }
  
  .flex{
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .flex1{
    display: flex;
    justify-content: space-between;
  }

  .flex1 li :hover{
    background-color: #4ba07b;
    color: white;
    
  
  }


  .footer-section h3 {
    font-size: 18px;
    margin-bottom: 10px;
  } 
  
  .footer-section input{
    padding: 5px;
    border: 1px solid black;
  }
  
  .footer-section NavLink{
    border: 1px solid black;
  }
  .footer-section p{
    margin-block: 5px;
  }