.product-display-container{
  display: flex ;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fbfbfb;
}


.product-display {
    display: flex;
    justify-content: center;
    
    align-items: center;
    padding: 20px;
    border: 1px solid #ccc;
    background-color:#4ba07b;
    border-radius: 8px;
   
    margin: 20px;
    border-radius: 5px;
  }
  .small-product-images{
    display: flex;
    gap: 10px;
    
  }

  .small-product-images img{
    height: 100px;
    width: 100px;
  }
  .product-display-image {
  height: 500px;
    object-fit: cover;
  }
  
  .product-details {
    flex: 1;
    margin-left: 20px;
  }
  
  .product-name {
    font-size: 24px;
    margin-bottom: 10px;
    
  }
  
  .product-description {
    margin-bottom: 10px;
  }
  
  .product-price {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .size-options {
    margin-bottom: 10px;
  }
  
  .size-select {
    padding: 8px;
    font-size: 16px;
  }
  
  .add-to-cart-button {
    background-color: #183a1d;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .add-to-cart-button:hover {
    background-color: #ff5314;
    color: black;
  }
  