/* Breadcrum.css */

.breadcrum {
  display: flex;
  align-items: center;
  font-size: 16px;
  color: #333;
  margin-bottom: 20px;
  margin-top: 20px;
}

.breadcrum img {
  width: 20px; 
 
  margin: 0 5px; 
}


.breadcrum span:last-child {
  font-weight: bold;
  color: #000; 
}
