.container{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #4ba07b;
}


.hero-img{
    max-width: 100vw;
    max-height: 100vh;
    margin-bottom: 40px;
 
}

.hero-img img{
    width: 100%;
    height: 100%;
    object-fit:cover;
    background-position: center;
}