.navbar {
    display: flex;
    font-size: 18px;
    align-items: center;
    justify-content: space-between;
    padding:15px;
    text-decoration: none;
    background-color: #4ba07b;
   
}

.nav-menu {
    display: flex;
    align-items: center;
    gap: 50px;
    text-decoration: none;
    list-style: none;
}

.nav-menu li {
    text-decoration: none;
}

.nav-menu li a {
    color:#000000; 
    text-decoration: none; 
}
.nav-menu li:hover {
    transform: translateY(-5px);
  
    
}

.nav-login-cart {
    display: flex;
    align-items: center;
    gap: 50px;
    text-decoration: none;
}



.nav-login-cart .profile,
.nav-login-cart .cart-logo {
    height: 35px;
    width: 35px;
    color: #000000; 
}

.nav-logo {
    height: 75px;
    width: 75px;
}

.nav-logo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
     mix-blend-mode:multiply;
    
    
}

.nav-menu li:hover a {
    text-decoration: underline;
}

/* Media Queries for Mobile Devices */
@media (max-width: 768px) {
    .navbar {
        flex-direction: column;
        align-items: flex-start;
        margin: 10px;
    }

    .nav-logo {
        height: 60px;
        width: 60px;
    }

    .nav-menu {
        flex-direction: column;
        gap: 20px;
        margin-top: 10px;
    }

    .nav-login-cart {
        flex-direction: row;
        gap: 20px;
        margin-top: 10px;
    }
}

@media (max-width: 480px) {
    .navbar {
        margin: 5px;
    }

    .nav-logo {
        height: 50px;
        width: 50px;
    }

    .nav-menu {
        gap: 15px;
    }

    .nav-login-cart {
        gap: 15px;
    }

    .nav-login-cart .profile,
    .nav-login-cart .cart-logo {
        height: 30px;
        width: 30px;
    }
}
